import React, { memo } from "react";
import {
  DualRangeSliderSection,
  Flex,
  RangeValue,
  SliderLineContainer,
  SliderLineWrapper,
  SliderLine,
  SelectedSliderLine,
  LeftSliderIcon,
  RightSliderIcon
} from './styles';

const View = props => {
  const isTouchSupported = !!('ontouchstart' in window);
  const start = isTouchSupported ? 'onTouchStart' : 'onMouseDown';
  const up = isTouchSupported ? 'onTouchEnd'   : 'onMouseUp';
  const move = isTouchSupported ? 'onTouchMove'  : 'onMouseMove';
  const eventListners = {
    [start]: props.onTouchStart,
    [move]: props.onMove,
    [up]: props.onTouchEnd,
  };
  const sliderIconScaling = activeRange => props.isActiveRange(activeRange) ? 2 : 1;
  return (
    <DualRangeSliderSection>
      <Flex>
        <RangeValue>From {props.getRange().start} years</RangeValue>
        <RangeValue textAlign="right">To {props.getRange().end} years</RangeValue>
      </Flex>
      <SliderLineContainer>
        <SliderLineWrapper {...eventListners}>
          <SliderLine ref={props.wrapperRef} >
            <SelectedSliderLine left={`${props.rangeStartLeft}%`} width={`${props.selectedRangeWidth}%`} />
            <LeftSliderIcon
              left={`${props.rangeStartLeft}%`}
              scale={sliderIconScaling('rangeStartLeft')}
            />
            <RightSliderIcon
              positionFrom={`${props.rangeEndLeft === 100 ? 'right' : 'left'}`}
              positionAt={`${props.rangeEndLeft === 100 ? 0 : `${props.rangeEndLeft}%`}`}
              scale={sliderIconScaling('rangeEndLeft')}
            />
          </SliderLine>
        </SliderLineWrapper>
      </SliderLineContainer>
      <div className='range-ruler'>
        <div className='range-start' />
        <div className='range-end' />
      </div>
    </DualRangeSliderSection>
  );
};

export default memo(View);